/**
 * Components Comments
 *
 */

.comments-area {
	h3 {
	    text-transform: initial;
	    font-size: 20px;
	}

	.comment-single {
		@extend %mt-half;
	}

	.comment-meta {
		h6 {
			font-size: 15px;
			margin-bottom: 5px;
			text-transform: capitalize;

			+ span {
			    color: #777;
			}
		}
	}

	.comment-reply {
		a {
		    font-size: 12px;
		    font-weight: 600;
		    text-transform: capitalize;
		}
	}

	.comment-content {
		padding: 20px;
		@include cx-default-color(bgc, offset);
		> p {
			margin-top: 10px;
		}
	}

	.comment-list {
		&.nested {
			@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
				display: block;
				width: 100%;
			}
		}
	}
}
